.eero-title {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}
.eero-system {
  background-color: rebeccapurple;
  width: 1440px;
  height: 1179px;
  padding-right: 188px;
  padding-left: 188px;
  padding-top: 580px;
  margin-left: auto;
  margin-right: auto;
}
.eero-list {
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.eero-list-item {
  width: 424px;
}
.link-compare {
  text-align: center;
}
